.App {
  text-align: center;
  width: 100%;
  height: 100%;
  background-color: aliceblue;
}
.aliceblue{
  color: aliceblue;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main{
  display: flex;
  align-items: center;
  flex-direction: column;
  flex-wrap: wrap;
  width: 100%;
  height: 92%;
  padding: 20px;
  margin: 0;
  background-color: #eee;
}

.graph{
  border: 5px solid;
  width: 70%;
  height: 100%;
  background-color:rgb(230, 224, 141);
  border-radius: 50px;
  padding: 20px;
  vertical-align: middle;
}

.bar{
  width: 100%;
}

.water{
  border: 5px solid;
  width: 28%;
  height: 60%;
  background-color: #a6eba6;
  border-radius: 50px;
  margin-left: 20px;
}

.point{
  border: 5px solid;
  width: 28%;
  height: 38%;
  background-color: #9be2f6;
  border-radius: 50px;
  margin-left: 20px;
  margin-top: 10px;
  flex-direction: column;
}

.point_icon{
  font-size: 80px;
  margin: 2px;
}

.graph-insideBox{
  border-radius: 50px;
  background-color: #eee;
  border: 0;
  vertical-align: middle;
  height: 90%;
}

.point-container{
  display: flex;
  justify-content: center;
}

.title{
  height: 100vh;
  justify-content: center;
  padding-top: 25%;
}

.sentence{
  margin-top: 20px;
  height: 50vh;
}

.progress1{
  width: 50%;
}

.progress2{
  width: 90%;
}

.grid{
  display: flex;
}

.sketch{
  width: 67%;
}

.initialSketch{
  width: 100%;
}

.content{
  height: 100vh;
}

.final{
  width: 40%;
}

.gridSentence{
  width: 40%;
  text-align: left;
}

.video{
  width: 70%;
}

.dashboard{
  width: 100%;
}

.evaluation{
  width: 80%;
}